import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useAdmissionPendingFeesReminderHistory } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import { nextPage } from "../../../Utils/Functions/nextPage";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import PendingFeesReminderMessageHistoryCard from "./PendingFeesReminderMessageHistoryCard";
import style from "../../FinanceManager/FinanceManager.module.css";
const PendingFeesReminderMessageHistory = ({ instituteId }) => {
  const { t } = useTranslation();
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView();

  const {
    admissionPendingFeesReminderHistory,
    admissionPendingFeesReminderHistoryLoading,
  } = useAdmissionPendingFeesReminderHistory({
    data: {
      id: instituteId,
      page: page,
      limit: 10,
    },
    skip: !instituteId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionPendingFeesReminderHistory?.list?.length) {
      let obj = nextPage(admissionPendingFeesReminderHistory?.list?.length);
      setState(() => obj);
    }
  }, [admissionPendingFeesReminderHistory?.list?.length]);
  return (
    <UserMemberContainerWrapper>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("reminder_message_history")}</h6>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0.3rem",
        }}
      >
        {admissionPendingFeesReminderHistory?.list?.map((data, index) =>
          admissionPendingFeesReminderHistory?.list?.length === index + 1 ? (
            <div key={data?._id} ref={ref}>
              <PendingFeesReminderMessageHistoryCard data={data} />
            </div>
          ) : (
            <PendingFeesReminderMessageHistoryCard
              data={data}
              key={data?._id}
            />
          )
        )}
        {admissionPendingFeesReminderHistoryLoading ? (
          <Loading />
        ) : (
          admissionPendingFeesReminderHistory?.list?.length === 0 && (
            <EmptyMessage
              customStyleContainer={{
                marginTop: "1.5rem",
              }}
              title="No student messages added."
            />
          )
        )}
      </UserMemberContentWrapper>
    </UserMemberContainerWrapper>
  );
};

export default PendingFeesReminderMessageHistory;
